import {initModel} from './initModel'
import {JsonOutputParser, StringOutputParser} from "@langchain/core/output_parsers";
import {genDataWithHeaderPrompt,genDataWithoutHeaderPrompt} from './genDataPrompt'


export async function genFakeData(isHeader, model,fileType,lines,dataTypes) {
    const outputParser = new StringOutputParser();
    let chain;
    console.log("----model is ----" + model)
    console.log("----dataType is---" + JSON.stringify(dataTypes))
    dataTypes.forEach(item => {console.log(item)})
    console.log("----dataTypes is---" +dataTypes)
    if(isHeader) {
        chain = genDataWithHeaderPrompt.pipe(initModel(model)).pipe(outputParser)
    } else {
        chain = genDataWithoutHeaderPrompt.pipe(initModel(model)).pipe(outputParser)
    }
    let result = await chain.invoke(
        {
            fileType: fileType,
            lines: lines,
            dataTypes: JSON.stringify(dataTypes)
        }
    )
    return result
}



