import React, {useState} from 'react';
import "../styles/global.css"
import "../styles/field.css"
const DataFormat = ({data,onUpdate}) => {
    const [isChecked, setIsChecked] = useState(data.hasHeader);
    const handleCheckboxChange = () => {
        setIsChecked(!isChecked);
        onUpdate({...data, hasHeader: isChecked});
    };

    const handleLineChange = (event) => {
        const { value } = event.target;
        onUpdate({ ...data, lines: value });
    }

    const handleFormatSelectChange = (event) => {
        const { value } = event.target;
        onUpdate({ ...data, format: value });
    }

    const handleLineEndingChange = (event) => {
        const { value } = event.target;
        onUpdate({ ...data, lineEnding: value });
    }
    const handleModelChange = (event) => {
        const { value } = event.target;
        onUpdate({...data,model: value})

    }

    return (
    <div className="container">
        <label>Model:</label>
        <select class = "dataFormat-select" onChange={handleModelChange}>
            <option>gpt-3.5-turbo</option>
            <option>gpt-4-turbo</option>
            <option>moonshot-v1-8k</option>
            <option>moonshot-v1-32k</option>
            <option>GLM-3-Turbo</option>
            <option>GLM-4</option>
        </select>
        <label>Rows:</label>
        <input id="lines" name="lines" value={data.lines} className="dataFormat-input" onChange={handleLineChange}></input>
        <label>Format:</label>
        <select value={data.format} className="dataFormat-select" onChange={handleFormatSelectChange}>
            <option>CSV</option>
            <option>JSON</option>
        </select>
        <label>Line Ending:</label>
        <select value={data.lineEnding} className="dataFormat-select" onChange={handleLineEndingChange}>
            <option>Unix(LF)</option>
            <option>Window(CRLF)</option>
        </select>
        <label>Include:</label>
        <label className="dataFormat-label"><input type="checkbox" className="dataFormat-checkbox" checked={data.hasHeader} onChange={handleCheckboxChange}></input>header</label>
    </div>
)
}
export default DataFormat;
