import {ChatPromptTemplate} from "@langchain/core/prompts";

export const genDataWithHeaderPrompt=ChatPromptTemplate.fromMessages([
    ["system","you are data generate expert, you can generate data base on given data type"],
    ["user","create {fileType} format data base on field name,field type, field range,should add data header.Below are data fields and type. data lines is {lines}. \n " +
    "dataTypes: \n" + "{dataTypes} \n" +
    "Only generate test data, do not generate any explanation"
    ]
])

export const genDataWithoutHeaderPrompt= ChatPromptTemplate.fromMessages([
    ["system","you are data generate expert, you can generate data base on given data type"],
    ["user","create {fileType} format data base on field name,field type, field range. Do not add data header.Below are data fields and type. data lines is {lines}. \n " +
    "dataTypes: \n" + "{dataTypes} \n" +
    "Only generate test data, do not generate any explanation, do not generate data header"
    ]
])
