import React , {useState} from 'react';
import '../styles/global.css'
import '../styles/field.css'
import '../component/Modal.js'
import Modal from "./Modal";

const ChooseTypeButton = ({data,onUpdate}) => {
    const [isModalOpen, setModalOpen] = useState(false);
    const [selectedType, setSelectedType] = useState('');
    const handleTypeSelected = (type) => {
        setSelectedType(type);
        onUpdate({ type: type });
    };
    const openModal = () => setModalOpen(true);
    const closeModal = () => setModalOpen(false);

    return (
        <div>
            <button className='chooseTypeButton' onClick={openModal} value={data.type}>
                {selectedType? selectedType: "Choose Type.."}
                <span className="dropdown-arrow">&#9660;</span>
            </button>
            <Modal
                isOpen={isModalOpen}
                onClose={closeModal}
                onTypeSelected={handleTypeSelected}
            />
        </div>
    );
};

const Field = ({ data, onRemove, onUpdate }) => {

    const handleNumberChange = (e) => {
        const inputValue = e.target.value;
        // 
        const regex = /^-?\d*(\.\d+)?$/;
        const filteredValue = inputValue.replace(/[^-\d.]/g, '');
        if (regex.test(filteredValue)) {
            const numericValue = parseFloat(filteredValue);
            onUpdate({ blank: numericValue });
        } else {
            console.error("Invalid input. Please enter a valid number.");
        }
    };

    return (
        <div className="container">
            <input className="field-input"
                value={data.name}
                onChange={(e) => onUpdate({ name: e.target.value})}
            />
            <ChooseTypeButton data={data} onUpdate={onUpdate}/>
            <div>
                <label>blank:</label>
                <input className="blank-input" value={data.blank} onChange={handleNumberChange}></input>
                <span>%</span>
            </div>
            <button className="close-button" onClick={onRemove}>
                <span className="bigger-symbol">&#9747;</span>
            </button>
        </div>
    );
};

export default Field;
