import React , {useState} from 'react';
import '../styles/modal.css';
import Block from './Block'
import BLOCKDATA from '../constants/blockData'

const Modal = ({ isOpen, onClose, onTypeSelected }) => {
    const [searchTerm, setSearchTerm] = useState('');
    const [selectedData, setSelectedData] = useState(BLOCKDATA.Basic);
    const handleSearchChange = (event) => {
        setSearchTerm(event.target.value);
    };
    const handleSideButtonClick = (key) => {
        setSelectedData(BLOCKDATA[key]);
    };

    const handleTypeClick = (type) => {
        onTypeSelected(type);
        onClose();
    };
    const splitIntoChunks= (data, number) => {
        const entriesArray = Object.entries(data);
        const chunks = [];

        for (let i = 0; i < entriesArray.length; i += number) {
            chunks.push(entriesArray.slice(i, i + number));
        }
        return chunks;
    };
    return (
        <div className={`modal ${isOpen ? 'open' : ''}`}>
           <div className="model-container">
             <div className="modal-header">
                <label className="big-label">Choose Type</label>
                <div className="header-right">
                <input
                    type="text"
                    placeholder="Find Type..."
                    onChange={handleSearchChange}
                />
                <button className="modal-close" onClick={onClose}>
                    <span className="bigger-symbol">&#9747;</span>
                </button>
                </div>
             </div>
             <div className="modal-content-container">
               <div className="modal-sidebar">
                <button className="modal-sidebar-button" onClick={()=> handleSideButtonClick("Basic")}>Basic</button>
                <button className="modal-sidebar-button" onClick={() => handleSideButtonClick("Car")}>Car</button>
                <button className="modal-sidebar-button" onClick={() => handleSideButtonClick("Commerce")}>Commerce</button>
                <button className="modal-sidebar-button" onClick={() => handleSideButtonClick("IT")}>IT</button>
                <button className="modal-sidebar-button" onClick={() => handleSideButtonClick("Health")}>Health</button>
                <button className="modal-sidebar-button" onClick={() => handleSideButtonClick("Location")}>Location</button>
               </div>
                 {selectedData && <Block data={splitIntoChunks(selectedData,3)} onTypeClick={handleTypeClick} />}
             </div>
           </div>
        </div>
    );
};

export default Modal;
