import './styles/global.css';
import './styles/sidebar.css'
import React, { useState } from 'react';
import DataGenerator from './component/DataGenerator.js';



function App() {
    const [activeMenu, setActiveMenu] = useState('menu1');

    return (
        <div className="app">
            <div className="sidebar">
                <label>TestData</label>
                <button onClick={() => setActiveMenu('menu1')}>Gen Fake Data</button>
                <button onClick={() => setActiveMenu('menu2')}>Gen SQL</button>
            </div>
            <div className="main-content">
                {activeMenu === 'menu1' ? <DataGenerator /> : <Menu2UI />}
            </div>
        </div>
    );
}

function Menu1UI() {
    return <div className="menu1-ui">Menu1 UI content</div>;
}

function Menu2UI() {
    // Implementation of Menu2 UI
    return <div className="menu2-ui">Menu2 UI content</div>;
}

export default App;
