import React from 'react';

const Block = ({ data,onTypeClick }) => {
    const handleTypeClick = (type) => {
        onTypeClick(type);
    };
    return (
        <div className="modal-content">
            {Object.entries(data).map(([category, values]) => (
                <div className="modal-content-row" key={category}>
                    {values.map(([type, items]) => (
                        <div className="modal-content-block" key={type}>
                            <button className="type-button" onClick={() => handleTypeClick(type)}>
                                {type}
                            </button>
                            {Array.isArray(items) ? (
                                items.map((item, i) => (
                                    <label className="little-label" key={i}>{item}</label>
                                ))
                            ) : (
                                <label className="little-label">{items}</label>
                            )}
                        </div>
                    ))}
                </div>
            ))}
        </div>
    );
};

export default Block;
