import React, { useState } from 'react';
import Field from './Field';
import '../styles/global.css'
import DataFormat from '../component/DataFormat'
import '../styles/dataGenerate.css'
import {genFakeData} from "../services/genDataChain";

const DataGenerator = () => {
    const [formatData,setFormatData] = useState({
            lines:10,
            format:'CSV',
            lineEnding: 'Unix(LF)',
            hasHeader: false,
            model: 'gpt-3.5-turbo'}
    )
    const updateFormatData = (newFormatData)=>{
        setFormatData(newFormatData)
    }

    const [fields, setFields] = useState([{
        name: '',
        type: '' ,
        blank:0}]);

    const addField = () => {
        setFields([...fields, {
            name: '',
            type: '',
            blank: 0 }]);
    };

    const removeField = (index) => {
        const newFields = fields.filter((_, idx) => idx !== index);
        setFields(newFields);
    };

    const updateField = (index, data) => {
        const newFields = [...fields];
        newFields[index] = { ...newFields[index], ...data };
        setFields(newFields);
    };

    const generateData = async () => {
        await genData(formatData, fields); // Pass formatData and fields to genData
    };
    const removeAll= () => {setFields([])};

    return (

        <div>
            <div className="title-container">
                <label>Name</label>
                <label>Type</label>
                <label>Options</label>
            </div>
            {fields.map((field, index) => (
                <Field
                    key={index}
                    data={field}
                    onRemove={() => removeField(index)}
                    onUpdate={(data) => updateField(index, data)}
                />
            ))}
            <div className="container">
            <button className="big-button" onClick={addField}>AddFiled</button>
                <button className="big-button" id="genData" onClick={generateData}>GenData<div id="spinner" className="spinner" hidden></div></button>
            <button className="big-button" onClick={removeAll}>RemoveAll</button>
            </div>
            <DataFormat data={formatData} onUpdate={(formatData) => updateFormatData(formatData)}></DataFormat>
        </div>
    );
};
export default DataGenerator;

const genData = async (formatData,fields)=> {
    const spinner = document.querySelector('#spinner');
    const genDataButton= document.querySelector('#genData');
    let data;
    try {
        spinner.hidden = false;
        genDataButton.disable=true;
        data=  await genFakeData(formatData.hasHeader,formatData.model,formatData.format,formatData.lines,fields)
    } catch (error) {
        console.error(error)
    }finally
    {
        spinner.hidden=true;
        genDataButton.disable=false;
    }
    const csvContent = convertToCSV(data);
    const blob = new Blob([csvContent], { type: 'text/csv' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.setAttribute('download', 'generated_data.csv');
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
}

function convertToCSV(data) {
    const lines = data.split('\n');
    const csvRows = [];
    lines.forEach(line => {
        const values = line.split(',');
        const csvLine = values.map(value => `${value}`).join(',');
        csvRows.push(csvLine);
    });
    const csvContent = csvRows.join('\n');
    return csvContent;
}
